import { decryption } from "../Encryption/decrypt";
import { PATCH, POST } from "../UrlConfig/MethodType";

export const storeActivateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/store/ins/${data.instituteId}/store/query`,
      method: POST,
      body: data.activateModule,
    }),
  });
};

export const storeUpdateModuleFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/edit/staff/stores/staff/${data.osid}?nsid=${data.nsid}`,
      method: PATCH,
    }),
    invalidatesTags: ["Store_Module"],
  });
};

// dashboard master api
export const storeDashboardMasterFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/store/ins/${data.stid}/master/query?mod_id=${data.mod_id}`,
    providesTags: ["Store_Module"],
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response) => {
      let dect = decryption(response?.encrypt);
      return dect?.store;
    },
  });
};

// master category api
export const storeMasterAddCategoryFunction = (builder) => {
  return builder.mutation({
    query: (data) => ({
      url: `/store/${data.stid}/new/good/category/query`,
      method: POST,
      body: data.bodyParameter,
    }),
  });
};

export const storeMasterCategoryListFunction = (builder) => {
  return builder.query({
    query: (data) =>
      `/store/${data.stid}/all/good/category/query?page=${data.page}&limit=${data.limit}&search=${data.search}`,
    serializeQueryArgs: ({ endpointName }) => {
      return endpointName;
    },
    transformResponse: (response, meta, arg) => {
      if (arg?.search)
        return {
          search: true,
          list: response?.all_good,
        };
      else
        return {
          page: arg?.page,
          list: response?.all_good,
        };
    },
    merge: (currentCache, incomingData) => {
      if (incomingData?.search) return incomingData;
      else if (incomingData?.page === 1) return incomingData;
      else return void currentCache?.list?.push(...incomingData?.list);
    },

    forceRefetch({ currentArg, previousArg }) {
      return currentArg !== previousArg;
    },
  });
};

